}
.menu-control div .ui.secondary.menu .item:hover {
    color: var(--not-text-color);
    background-color: var(--main-color);
    border: 1px solid var(--not-text-color); 
}
.menu-control div .ui.secondary.menu .active.item:hover {
    color: var(--not-text-color);
    background-color: var(--main-color);
    border: 1px solid var(--not-text-color); 
}
.menu-control div .ui.secondary.menu .active.item{
    color: var(--text-color);
 
    border: 1px solid white;
    border-radius: 30px;
    background-color: var(--side-color);
    transition: all .1s ease-in;
    
}
